document.addEventListener('DOMContentLoaded', function () {
  const productOverviewBody = document.querySelector('.cmp-product-overview__body');
  const stickyNav = document.querySelector('.cmp-game-fps-sticky-navigation');
  let floatingNavHeight = stickyNav.offsetHeight;

  if (productOverviewBody) {
    const intersectionObserver = new IntersectionObserver((entries) => {
      if (entries[0].intersectionRatio <= 0) {
        document.querySelector('.cmp-game-fps-sticky-nav').style.display = 'block';
        document.documentElement.style.setProperty('--floatingNavHeight', floatingNavHeight + 'px');
      } else {
        document.querySelector('.cmp-game-fps-sticky-nav').style.display = 'none';
        document.documentElement.style.setProperty('--floatingNavHeight', 0 + 'px');
        const mobileAccordionItem = document.querySelector('.mob-accordion-item');
        mobileAccordionItem.classList.remove('open');
      }
    });
    intersectionObserver.observe(document.querySelector('.cmp-product-overview__body'));
  }
  document.addEventListener('floatingNavAccordionToggle', function (event) {
    if (stickyNav) {
      const newHeight = stickyNav.offsetHeight;
      document.documentElement.style.setProperty('--floatingNavHeight', newHeight + 'px');
    }
  });
});
