document.addEventListener('DOMContentLoaded', function () {
  document.querySelectorAll('.mob-accordion-item').forEach((item) => {
    item.querySelector('.mob-accordion-item-header').addEventListener('click', () => {
      const wasOpen = item.classList.contains('open');
      item.classList.toggle('open');
      // Dispatch custom event when accordion opens/closes
      const accordionEvent = new CustomEvent('floatingNavAccordionToggle', {
        detail: {
          isOpen: !wasOpen,
          element: item,
          type: 'mob-accordion-item',
        },
      });

      document.dispatchEvent(accordionEvent);
    });
  });
});
